@font-face {
  font-family: HomemadeSausage;
  src: url(../fonts/HomemadeSausage.ttf);
}
html {
  font-family: HomemadeSausage;
  font-size: 16px;
  scroll-behavior: smooth;
}

.hr {
  margin-block: 10px;
  width: 80%;
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid gray;
}

#intro {
  display: flex;
  background-image: linear-gradient(rgb(255, 255, 255), rgb(221, 246, 253), rgb(255, 255, 255), rgb(221, 221, 221), rgb(255, 255, 255));
  align-items: center;
  margin-top: 5%;
  margin-bottom: 1%;
  margin-inline: 5%;
  box-shadow: inset 0px 0px 5px 1px rgb(153, 153, 153), 0px 0px 5px 1px rgb(153, 153, 153);
  border: none;
  border-radius: 15px;
}
#intro > .center {
  display: flex;
  flex-direction: column;
  text-align: justify;
  margin-inline: 3%;
}
#intro > .center h1 {
  text-align: center;
}
#intro > .center p {
  font-style: italic;
  margin-inline: 3%;
  margin-bottom: 3%;
}

.showAndHideMenu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(0.25turn, #b4b4b4, 10%, #ebf8e1, #616161);
  padding-right: 3%;
  color: rgb(61, 61, 61);
}
.showAndHideMenu p, .showAndHideMenu button {
  margin-inline: 10px;
}

.img {
  width: 80%;
  height: auto;
  display: inline-block;
  position: relative;
  top: 1%;
  left: 25%;
  box-shadow: inset 0px 0px 7px 2px rgb(153, 153, 153), 0px 0px 7px 2px rgb(153, 153, 153);
  border-radius: 10px;
  cursor: pointer;
}
.img:hover {
  opacity: 0.5;
  transform: scaleX(-1);
}
.img:active {
  transform: scale(1.3);
}

body main {
  display: flex;
  flex-direction: column;
  justify-items: center;
}

.part1 {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: center;
  background-image: linear-gradient(white, lightblue, white);
}
.part1.img-container {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.part1 > .content-container {
  margin-left: 10%;
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  justify-items: flex-start;
}
.part1 > .content-container > p {
  font-size: 1rem;
  margin-bottom: 2vh;
}

.end {
  text-align: right;
  font-size: 1em;
  background: linear-gradient(0.25turn, #b4b4b4, 10%, #caf9ff, #5599ff);
  padding-right: 4%;
  color: rgb(0, 0, 0);
  padding-block: 5px;
}

.boardChess {
  line-height: 70%;
}

#languageAndMenu {
  z-index: 2;
  position: fixed;
  left: 0;
  top: 1%;
  width: 100%;
  background-image: linear-gradient(90deg, rgba(180, 180, 180, 0.7) 10%, rgba(202, 249, 255, 0.7), rgba(85, 153, 255, 0.7));
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  padding-right: 2%;
  height: 2.5em;
  margin-top: 1%;
}
#languageAndMenu p {
  font-weight: 700;
  border: 1px solid;
  width: auto;
  padding-inline: 5px;
  background-color: white;
  cursor: pointer;
}

.menu {
  z-index: 2;
  display: flex;
  align-items: center;
  align-content: center;
  flex-direction: row;
  background: rgba(255, 255, 255, 0.493);
  width: auto;
  height: 2.2em;
  box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.514);
  border-color: black;
  border-radius: 5px;
  border-style: 5px outset;
  padding: 0.5%;
  margin-left: 1%;
}
.menu h3 {
  margin-inline: 1vw;
}
.menu ol {
  margin-left: 5%;
  list-style-type: none;
  padding: 0;
  margin: 0;
  font-weight: 700;
  line-height: 2em;
  text-align: center;
}
.menu ol a {
  text-decoration: none;
  color: rgb(0, 0, 0);
}
.menu ol > li {
  float: left;
  background-image: linear-gradient(white, rgb(111, 221, 255));
  width: 150px;
  margin-left: 2px;
  border-radius: 10px;
  border: 3px outset rgb(155, 155, 155);
  box-shadow: 5px 5px 5px 0px grey;
}
.menu ol > li:first-child:active {
  background-image: radial-gradient(white 50%, rgb(251, 255, 1));
  transform: scale(1.3);
}
.menu ol > li:hover {
  background-image: linear-gradient(rgb(255, 255, 255), rgb(68, 134, 255));
}
.menu ol > li:hover > a {
  color: rgb(0, 0, 0);
  font-weight: bold;
}
.menu ol > li > a:hover {
  color: rgb(66, 0, 173);
}
.menu ol > li > ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.menu ol > li:first-child {
  margin-left: 0;
}
.menu ol > li > ul > li {
  z-index: 3;
  border: 3px outset rgb(155, 155, 155);
  border-radius: 10px;
  background-image: linear-gradient(white, rgb(184, 184, 184), white);
  box-shadow: 5px 5px 5px 0px grey;
}
.menu ol > li > ul > li:hover a {
  color: rgb(0, 0, 0);
  font-weight: bold;
}
.menu ol > li > ul > li:hover {
  background-image: linear-gradient(white, rgb(68, 134, 255), white);
}
.menu ol > li > ul > li:active {
  background-image: radial-gradient(white 50%, rgb(251, 255, 1));
  transform: scale(1.3);
}
.menu ol > li > ul {
  display: none;
}
.menu ol > li:hover > ul {
  display: block;
}
.menu ol > li {
  float: left;
  background-color: #fdd700;
  width: 150px;
  margin-left: 1px;
  height: 2em;
}

.menu2 {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: auto;
  margin-right: 1%;
}

button {
  color: rgb(77, 77, 77);
  cursor: pointer;
}

.button {
  border-radius: 15px;
  font-weight: 700;
  font-size: 1rem;
  background: radial-gradient(white 10%, 70%, #7abfff);
  width: auto;
  height: auto;
  transition: transform 1s;
  padding: 10px;
  text-align: center;
  border: 2px outset gray;
  box-shadow: 1px 1px 2px 1px black;
}
.button:hover {
  color: black;
  transform: scale(1.05);
  background: white;
}
.button:active {
  color: #8b0000;
  background: rgb(255, 139, 139);
  transform: scale(1.3);
}

#show1, #show2, #show3, #show4, #show5 {
  background: radial-gradient(white 10%, 70%, gray);
  width: auto;
  height: auto;
  opacity: 1;
  border: 0px;
}
#show1:hover, #show2:hover, #show3:hover, #show4:hover, #show5:hover {
  background: white;
}
#show1:active, #show2:active, #show3:active, #show4:active, #show5:active {
  background: rgb(255, 139, 139);
}

.btnX {
  font-size: 1.5rem;
  outline: none;
  border: none;
  background: none;
  position: absolute;
  right: 15px;
  top: 15px;
  cursor: pointer;
}

.reset {
  visibility: hidden;
  -webkit-transform: scale(0);
  transform: scale(0);
  border-radius: 15px;
  font-weight: 700;
  font-size: 1rem;
  background: radial-gradient(white 10%, 70%, #7abfff);
  width: auto;
  height: 50px;
  transition: transform 1s;
  padding-block: auto;
  box-shadow: 1px 1px 2px 1px gray;
  margin-left: 3vw;
  border: 3px outset gray;
}

.r-active {
  visibility: visible;
  transform: scale(1);
}
.r-active:hover {
  color: black;
  transform: scale(1.05);
  background: white;
}
.r-active:active {
  color: #8b0000;
  background: rgb(255, 139, 139);
  transform: scale(1.1);
}

.overl {
  position: absolute;
  top: 0;
  left: 0;
}

.o-active {
  width: 100vw;
  height: 100vh;
  background: white;
  pointer-events: all;
  opacity: 0.5;
  z-index: 1;
}

.popup {
  visibility: hidden;
  width: 40vw;
  background-color: white;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) scale(0);
  transform: translate(-50%, -50%) scale(0);
  -webkit-transition: 200ms ease-in-out;
  transition: 200ms ease-in-out;
  -webkit-box-shadow: 0px 0px 7px 1px gray;
  box-shadow: 0px 0px 7px 1px gray;
  z-index: 2;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: left;
  -ms-flex-align: left;
  align-items: left;
  line-height: 1.6;
  padding: 2% 5% 2% 5%;
}
.popup > h2 {
  font-size: 2rem;
}
.popup > p {
  font-size: 1rem;
}
.popup > p > span {
  color: #8b0000;
  font-size: 1.5rem;
  font-weight: 700;
}

.active {
  visibility: visible !important;
  -webkit-transform: translate(-50%, -50%) scale(1);
  transform: translate(-50%, -50%) scale(1);
}

@font-face {
  font-family: fontBall2;
  src: url(../fonts/webdings.ttf);
}
@keyframes spinning {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes op {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.Ball {
  animation-name: spinning;
  border-radius: 80%;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in, ease-out;
  background-image: radial-gradient(#fff700, #ff0000, #afffb6);
}

.BallRunning {
  animation-name: spinning, op;
  border-radius: 80%;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  background-image: radial-gradient(#fff700, #ff0000, #afffb6);
}

.Ball:hover {
  cursor: pointer;
  animation-name: none;
  transform: scale(1.8);
}

.introBall2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.part2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-image: radial-gradient(#ff8765, #ffffff, #97fdff);
}
.part2 h2 {
  text-align: center;
}
.part2 p {
  text-align: center;
  margin-inline: 10%;
  text-justify: distribute;
}

.boardBall2 {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  margin-top: 1%;
  margin-bottom: 2%;
  width: auto;
  height: auto;
  font-family: fontBall2;
  font-size: 2em;
}

.Air {
  background-color: #afffb6;
}

.y {
  color: red;
  background-color: #fff347;
}

.Wall {
  background-color: #ce9980;
}

@media only screen and (max-width: 970px) {
  html {
    font-size: 12px;
  }
  #languageAndMenu {
    flex-direction: column;
  }
  .menu {
    border-style: 3px outset;
  }
  .menu ol > li {
    width: auto;
    line-height: auto;
    padding-inline: 3px;
    border: 2px outset rgb(155, 155, 155);
  }
  .menu2 {
    margin-top: 1%;
    width: auto;
  }
  #intro {
    margin-top: 15%;
  }
  .img {
    width: 50%;
    height: auto;
    margin-inline: auto;
    transform: scale(0.8);
  }
  .part1 {
    flex-direction: column;
  }
  .part1 > .content-container {
    text-align: center;
    width: 100%;
    width: 80%;
    margin-inline: auto;
    justify-content: center;
    justify-items: center;
  }
}